function Routes() {
  const { isExact, path } = useRouteMatch();
  const history = useHistory();
  // const background = location.state && location.state.background
  // console.log(background)

  const [loaded, setLoaded] = useState(false)
  useEffect(() => {
    if (!window.mdata) masterData(null, () => {
      setLoaded(true)
      // resetData()
    })
  }, [])

  useEffect(() => {
    if (path === '/') {
      history.push('/board');
    }

    if (!isAuthorized(path, isExact)) {
      history.push('/board');
    }
  }, [path]);

  return loaded ? <AllRoutes /> : null
}

function AllRoutes(props) {
  const [lang, setLang] = useState('en')
  const options = { lang, setLang }
  const theme = cs.newdesign
  theme.rtl = (lang == 'ar')
  theme.dir = theme.rtl ? 'rtl' : 'ltr'

  useEffect(() => {
    console.log("INSIDE ALL ROUTES EFFECT")
    langStore.setLang(lang)
  }, [lang])

  return (
    <>
      <ToastContainer
        position="top-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={true}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        limit={1}
      />
      <Switch>
        <Route path="/timeline"> <Header /> <TP theme={cs.home}> <Timeline /> </TP> </Route>
        <Route path="/kanban"> <Header /> <TP theme={cs.home}> <Kanban /> </TP> </Route>
        <Route path="/dashboard"> <TP theme={cs.home}> {dbForRole(lang, setLang)} </TP> </Route>
        <Route path="/formulation"> <Header /> <TP theme={cs.fs}> <Formulation /> </TP> </Route>
        <Route path="/collaboration"> <Header /> <TP theme={cs.cs}> <Collaboration /> </TP> </Route>
        <Route path="/activation"> <Header /> <TP theme={cs.as}> <Activation /> </TP> </Route>
        <Route path="/governance"> <Header /> <TP theme={cs.gs}> <Governance /> </TP> </Route>
        <Route path="/compliance"> <Header /> <TP theme={cs.cps}> <Compliance /> </TP> </Route>
        <Route path="/test"> <Header /> <TP theme={cs.cps}> <Test /> </TP> </Route>
        <Route path="/hmc"> <TP theme={cs.cps}> <Hmc /> </TP> </Route>
        <Route path="/pcp"> <TP theme={cs.cps}> <Pcp /> </TP> </Route>
        <Route path="/bcp"> <TP theme={cs.cps}> <Bcp /> </TP> </Route>
        <Route path="/entities"> <TP theme={cs.newdesign}> <Entity /> </TP> </Route>
        <Route path="/projects"> <TP theme={cs.newdesign}> <Projects /> </TP> </Route>
        <Route path="/cases"> <TP theme={cs.newdesign}> <Cases /> </TP> </Route>
        <Route path="/issues"> <TP theme={cs.newdesign}> <Issues /> </TP> </Route>


        {/* {'Just Added for Testing'} */}
        <Route path="/policies/case/:caseId"> <TP theme={cs.newdesign}> <ProjectCase  /> </TP> </Route>
        <Route path="/policies"> <TP theme={cs.newdesign}> <Policy /> </TP> </Route>
        <Route path="/selected/policy">  <TP theme={cs.newdesign}> <SelectedPolicy/> </TP>  </Route>
        {/* {'Just Added for Testing'} */}

        <Route path="/resources" exact> <TP theme={cs.newdesign}> <Resources /> </TP> </Route>
        <Route path="/a-dashboard">
          <TP theme={cs.cps}>
            {dbForRole(lang, setLang)}
          </TP>
        </Route>

        <Route path="/qg/:entity_id/:project_id"> <TP theme={theme}> <Qg {...options} /> </TP> </Route>
        <Route path="/board"> <TP theme={theme}><Board {...options} /> </TP> </Route>
        <Route path="/agency/:entity_id"> <TP theme={theme}> <Agency {...options} /> </TP> </Route>
        <Route exact path="/"> <TP theme={cs.home}> {dbForRole(lang, setLang)} </TP></Route>
      </Switch>
    </>)
}



function dbForRole(lang, setLang) {
  if (isMOTCUser()) {
    return <MotcHome />
    // return <JawdaHome />
  } else if (isJAWDAUser()) {
    return <jawdaHome />
  } else {
    return <AgencyHome />
  }
}
function App() {
  return (
    <Router>
      <Switch>
        {/* <Route path='/home'> <Home1 /></Route> */}
        <Route path='/login'><Login /></Route>
        <Route path="/set-password/:token"> <TP theme={cs.newdesign}> <SetPassword /> </TP> </Route>
        <Route path="/forgot-password"> <TP theme={cs.newdesign}> <ForgotPassword /> </TP> </Route>
        <Route path={['/dashboard', '/a-dashboard', '/board', '/qg/:entity_id/:project_id', '/agency/:entity_id', '/entities/:entity_id/details', '/entities/:entity_id', '/entities', '/projects/:project_id/compliance-project-details', '/projects/:project_id/compliance-projects', '/projects/:project_id/case-management', '/projects/:project_id', '/projects', '/resources', '/policies', '/cases', '/']}
          render={({ location }) =>
            loggedIn() ? (
              <Routes />
            ) : (
              <Redirect
                to={{
                  pathname: "/login",
                  state: { from: location }
                }}
              />
            )
          }
        />
      </Switch>
    </Router>
  );
}

export default App;
